.header {
  background-color: #262626;
  padding: 9px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 9px;
  position: absolute;
  top: 10px;
  right: 19px;
  cursor: pointer;
}

.logo {
  height: 139px;
  width: 219px;
  transition: box-shadow 0.3s ease;
}

.logo:hover {
  box-shadow: 0 0 19px 9px #00ff85;
}
